import React from "react";

class Mental extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 1
        }
    }

    clickNext = () => {
        if (this.state.count === 3) {
            this.setState({
                count: 1
            });
        } else {
            this.setState({
                count: this.state.count + 1
            });
        }
    };

    clickPrev = () => {
        if (this.state.count === 1) {
            this.setState({
                count: 3
            });
        } else {
            this.setState({
                count: this.state.count - 1
            });
        }
    };

    render() {
        return (
            <div>
                <div>
                    <h1>Mental</h1>
                </div>
                <div className="content">
                    <img src={"https://cdn.discordapp.com/attachments/387050226956894211/768448873399058462/unknown.png"} width="500"
                         alt={"https://cdn.discordapp.com/attachments/387050226956894211/768448873399058462/unknown.png"}/>
                    <div>
                        <MentalText count={this.state.count}/>
                        <span className="button" onClick={this.clickPrev}>
                            Prev
                        </span>
                        <span className="button" onClick={this.clickNext}>
                            Next
                        </span>
                    </div>
                    <img src={"https://img1.pnghut.com/25/24/9/0qmXbuPEYS/psychology-learning-nose-silhouette-smile.jpg"} width="500"
                         alt={"https://img1.pnghut.com/25/24/9/0qmXbuPEYS/psychology-learning-nose-silhouette-smile.jpg"}/>
                </div>
            </div>
        )
    }
}

const MentalText = (props) => {
    if (props.count === 1) {
        return (
            <div>
                <p>I learned things like how the brain has multiple ways to store information, short term or long term.
                    I realized there are some subjects where I basically only have to use short term memory and
                    regurgitate the information and forget it afterwards to pass and others where I really learn and
                    know how to apply what I’ve learned and so I have realized that subjects that give me opportunities
                    to do the latter instead of the former is preferred.</p>
            </div>
        )
    } else if (props.count === 2) {
        return (
            <div>
                <p>I also learned that there is the theory of multiple intelligences according to the quiz Intrapersonal
                    seems to be my highest score which I could understand seeing how introspective I may be.</p>
            </div>
        )
    } else if (props.count === 3) {
        return (
            <div>
                <p>I learned of the terms self-efficacy and human agency. Self-efficacy being from what I understand
                    basically the extent of which an individual believes they can learn something. I realize just how
                    important having a high self efficacy is as without it one would probably give up learning something
                    as soon as one found it too hard or didn’t understand something. If I were to relate it to myself
                    there are sometimes days when I’m trying to get something to work but fail to when trying to learn
                    something. But even still I shouldn’t immediately give up because failing and learning from one’s
                    mistakes is just part of learning. If I have realized this, then that probably means I have a high
                    self-efficacy.</p>
            </div>
        )
    }

};


export default Mental;