import React from "react";

class Spiritual extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 1
        }
    }

    clickNext = () => {
        if (this.state.count === 2) {
            this.setState({
                count: 1
            });
        } else {
            this.setState({
                count: this.state.count + 1
            });
        }
    };

    clickPrev = () => {
        if (this.state.count === 1) {
            this.setState({
                count: 2
            });
        } else {
            this.setState({
                count: this.state.count - 1
            });
        }
    };

    render() {
        return (
            <div>
                <div>
                    <h1>Spiritual</h1>
                </div>
                <div className="content">
                    <img
                        src={"https://cdn.discordapp.com/attachments/387050226956894211/768469243633074206/unknown.png"}
                        width="500"
                        alt={"https://cdn.discordapp.com/attachments/387050226956894211/768469243633074206/unknown.png"}/>
                    <div>
                        <MentalText count={this.state.count}/>
                        <span className="button" onClick={this.clickPrev}>
                            Prev
                        </span>
                        <span className="button" onClick={this.clickNext}>
                            Next
                        </span>
                    </div>
                    <img
                        src={"https://cdn.discordapp.com/attachments/387050226956894211/768474415561703464/unknown.png"}
                        width="500"
                        alt={"https://cdn.discordapp.com/attachments/387050226956894211/768474415561703464/unknown.png"}/>
                </div>
            </div>
        )
    }
}

const MentalText = (props) => {
    if (props.count === 1) {
        return (
            <div>
                <p>I already learned about how much the influence the people one is with while growing up has, but now I
                    know of even more aspects of that influence such as having pets growing up can lead to love for
                    animals or teaching the value of recycling while young can instill respect for the environment.</p>
            </div>
        )
    } else if (props.count === 2) {
        return (
            <div>
                <p>I also learned about the terms wellbeing and life satisfaction. Wellbeing being how good we feel
                    about ourselves so it basically relates to our self esteem and life satisfaction being contentment
                    with the circumstances and direction one’s life is headed. I realize that it’s a privilege and lucky
                    for me to be able to say that I’m doing fine at both seeing as I still have a good education I am
                    not wasting and food on our table and a roof over our heads which only made me appreciate what I
                    have even more.</p>
            </div>
        )
    }

};


export default Spiritual;