import React from "react";

class Emergent extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 1
        }
    }

    clickNext = () => {
        if (this.state.count === 2) {
            this.setState({
                count: 1
            });
        } else {
            this.setState({
                count: this.state.count + 1
            });
        }
    };

    clickPrev = () => {
        if (this.state.count === 1) {
            this.setState({
                count: 2
            });
        } else {
            this.setState({
                count: this.state.count - 1
            });
        }
    };

    render() {
        return (
            <div>
                <div>
                    <h1>Emergent</h1>
                </div>
                <div className="content">
                    <img
                        src={"https://cdn.discordapp.com/attachments/387050226956894211/768475154036031509/unknown.png"}
                        width="500"
                        alt={"https://cdn.discordapp.com/attachments/387050226956894211/768475154036031509/unknown.png"}/>
                    <div>
                        <EmergentText count={this.state.count}/>
                        <span className="button" onClick={this.clickPrev}>
                            Prev
                        </span>
                        <span className="button" onClick={this.clickNext}>
                            Next
                        </span>
                    </div>
                    <img
                        src={"https://cdn.discordapp.com/attachments/387050226956894211/768475670577676358/unknown.png"}
                        width="500"
                        alt={"https://cdn.discordapp.com/attachments/387050226956894211/768475670577676358/unknown.png"}/>
                </div>
            </div>
        )
    }
}

const EmergentText = (props) => {
    if (props.count === 1) {
        return (
            <div>
                <p>I learned that politics in a nation is shaped by the people and context within its people and
                    situation. This makes the politics of each nation different from each other as different nations
                    have different people, situations and culture.</p>
            </div>
        )
    } else if (props.count === 2) {
        return (
            <div>
                <p>I also learned of the term active citizenship which is a way for people to feel that they are “giving
                    back” something to their community and gives a sense of community and ethnic pride.</p>
            </div>
        )
    }

};


export default Emergent;