import React from "react";

class Social extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            count: 1
        }
    }

    clickNext = () => {
        if (this.state.count === 3){
            this.setState({
                count: 1
            });
        }else {
            this.setState({
                count: this.state.count + 1
            });
        }
    };

    clickPrev = () => {
        if (this.state.count === 1){
            this.setState({
                count: 3
            });
        }else {
            this.setState({
                count: this.state.count - 1
            });
        }
    };

    render() {
        return (
            <div>
                <div>
                    <h1>Social</h1>
                </div>
                <div className="content">
                    <img src={"https://webstockreview.net/images/families-clipart-black-and-white-1.png"} width="500"
                         alt={"https://webstockreview.net/images/families-clipart-black-and-white-1.png"}/>
                    <div>
                        <SocialText count={this.state.count}/>
                        <span className="button" onClick={this.clickPrev}>
                            Prev
                        </span>
                        <span className="button" onClick={this.clickNext}>
                            Next
                        </span>
                    </div>
                    <img src={"https://braidmission.org/wp-content/uploads/2017/03/circle-of-people.png"} width="500"
                         alt={"https://braidmission.org/wp-content/uploads/2017/03/circle-of-people.png"}/>
                </div>
            </div>
        )
    }
}

const SocialText = (props) => {
    if (props.count === 1) {
        return (
            <div>
                <p>I learned just how important and how much influence in your behaviour and beliefs and conceptions the
                    people that you are with as a young child. When one is that young, they still have no idea what is
                    right and what is wrong and etc and as such the initial influence of the people one grows up with
                    when young is huge. I really realize how privileged I am to have been born into a family with no
                    issues and was able to raise me well.</p>
            </div>
        )
    } else if (props.count === 2) {
        return (
            <div>
                <p>I learned about another aspect of one’s self known as the socio-digital self which and that a person
                    may be able behave differently online than in face to face. When I was younger I was initially quiet
                    not very spoken and usually only seened and read group chat messages eventually I learned to be able
                    to be more talkative in either situation and get along with people.</p>
            </div>
        )
    } else if (props.count === 3) {
        return (
            <div>
                <p>I learned about the material and economic self and which is basically how one’s financial situation
                    growing up influences the things a person values and their priorities and whatnot. Again I realized
                    just how privileged I am to have been in a family that can live comfortably enough to live
                    comfortable and also not too rich or anything that I end up growing terribly and not able to attempt
                    to fend for myself and has become one of my many motivations for wanting to do good in academics and
                    eventually get a stable income that can give back.</p>
            </div>
        )
    }

};


export default Social;