import React from "react";

class Biological extends React.Component {
    render() {
        return (
            <div>
                <div>
                    <h1>Biological</h1>
                </div>
                <div className="content">
                    <img
                        src={"https://cdn.discordapp.com/attachments/387050226956894211/768486825651208213/unknown.png"}
                        width="500"
                        alt={"https://cdn.discordapp.com/attachments/387050226956894211/768486825651208213/unknown.png"}/>
                    <BiologicalText/>
                    <img
                        src={"https://cdn.discordapp.com/attachments/387050226956894211/768487328192397322/unknown.png"}
                        width="500"
                        alt={"https://cdn.discordapp.com/attachments/387050226956894211/768487328192397322/unknown.png"}/>
                </div>
            </div>
        )
    }
}

const BiologicalText = () => {
    return (
        <div>
            <p>I have learned the concept of nature and nurture where nature is the traits that one has because they
                were pass down from one’s family and nurture being the traits that one has because of the specific way
                that person grew up and the conditions that person was raised in and the experiences that person has
                gained throughout their life</p>
        </div>
    )
};


export default Biological;