import React from "react";

class Emotional extends React.Component {
    render() {
        return (
            <div>
                <div>
                    <h1>Emotional</h1>
                </div>
                <div className="content">
                    <img
                        src={"https://cdn.discordapp.com/attachments/387050226956894211/768453122170814464/unknown.png"}
                        width="500"
                        alt={"https://cdn.discordapp.com/attachments/387050226956894211/768453122170814464/unknown.png"}/>
                     <EmotionalText />
                    <img
                        src={"https://cdn.discordapp.com/attachments/387050226956894211/768453636728029224/unknown.png"}
                        width="500"
                        alt={"https://cdn.discordapp.com/attachments/387050226956894211/768453636728029224/unknown.png"}/>
                </div>
            </div>
        )
    }
}

const EmotionalText = () => {
    return (
        <div>
            <p>I learned about how emotions can affect one’s initial attitude or reaction to something an example
                would be a knee jerk angry reaction to something and that if one keeps this in their head for a long
                time it might develop into resentment and bitterness for that thing. So I learned that it is
                important not to sow anger towards things.</p>
        </div>
    )
};


export default Emotional;